import GitHubIcon from '@material-ui/icons/GitHub'
import LinkedInIcon from '@material-ui/icons/LinkedIn'
import { about } from '../../portfolio'
import './About.css'
import profileImage from '../../my-image.jpg'

const About = () => {
  const { name, role, description1, description2, resume, social } = about

  return (
    <div className='about center'>
      <div className='about__image'>
        <img
          src={profileImage}
          alt={`${name}'s profile`}
          className='about__img'
        />
      </div>

      <div className='about__text'>
        {role && <h2 className='about__role'>{role}.</h2>}
        <p className='about__desc_part1'>{description1 && description1}</p>

        <p className='about__desc_part2'>{description2 && description2}</p>

        <div className='about__contact center'>

          {resume && (
            <a href={resume} target='_blank' rel='noopener noreferrer'>
              <span type='button' className='btn btn--outline'>
                Resume
              </span>
            </a>
          )}

          {social && (
            <>
              {social.github && (
                <a
                  href={social.github}
                  aria-label='github'
                  className='link link--icon'
                >
                  <GitHubIcon />
                </a>
              )}

              {social.linkedin && (
                <a
                  href={social.linkedin}
                  aria-label='linkedin'
                  className='link link--icon'
                >
                  <LinkedInIcon />
                </a>
              )}
            </>
          )}
        </div>
      </div>
    </div>
  )
}

export default About
